.home {
    position: relative;
    height: 100vh;
    //overflow: hidden;
    max-height: 1010px;

    &__picture {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url(/assets/images/home/block1/bg.jpg);
        background-repeat: no-repeat;
        background-size: 1920px;
        background-position: center;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__title-block {
        display: flex;
        position: relative;
    }

    &__price-title {
        display: flex;
    }

    &__title-left {
        position: relative;

        &:before {
            position: absolute;
            content: "";
            top: 34px;
            width: 44px;
            height: 1px;
            background: #0b1f2f;
        }

        p {
            font-family: "NotoSerifDisplay", sans-serif;
            font-style: normal;
            font-weight: 600;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            padding-left: 58px;
            z-index: 2;
            position: relative;
        }
    }

    &__content {
        width: 1140px;
        margin: 0 auto;
        padding: 190px 0;
    }

    .title-art {
        font-family: "NotoSerifDisplay", sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 130px;
        text-align: right;
        letter-spacing: -0.02em;
        line-height: 116px;
        position: relative;
        z-index: 2;
    }

    &__title-bottom {
        display: flex;
        margin-top: 34px;

        h1 {
            margin: 0;
            text-transform: inherit;
            padding-left: 25px;
            width: 460px;
            margin-right: 70px;
            letter-spacing: 0.1em;
            font-weight: 400;
            position: relative;
        }
    }

    &__popup {
        width: 296px;
        height: 57px;
        background: #0b1f2f;
        border-radius: 40px;
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 19px;
        text-align: center;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        margin-top: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #ffffff;

        span {
            font-size: 14px;
            line-height: 19px;
        }

        @media (max-width: 4500.98px) and (min-width: 1199.98px) {
            &:hover {
                span {
                    color: #0b1f2f;
                }
            }
        }
    }
}

.home.full .home__picture {
    //background-position-y: -115px;
}

.home.height-laptop .home__title-blur {
    width: 565px;
    height: 150px;
    left: -106px;
    top: -45px;
    background: #ffffff;
    background-blend-mode: exclusion;
    opacity: 0.4;
    filter: blur(68px);
}

.home__header-blur {
