}

.home__title-bottom {
    position: relative;
}

.home__title-blur {
    position: absolute;
    width: 958px;
    height: 160px;
    left: -150px;
    top: -45px;
    background: #ffffff;
    background-blend-mode: exclusion;
    opacity: 0.4;
