.home__popup.popup-chrome{
    position: relative;
    overflow: hidden;
    &:hover{
        cursor:pointer;
    }
}


.home__popup.popup-chrome:after, .home__popup.popup-chrome:before {
    content: "";
    position: absolute;
    transition-timing-function: cubic-bezier(.25,.74,.22,.99);
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    border-radius: 200px;
    pointer-events: none;
    transition-property: transform,background-color,border-color,box-shadow;
    transition-duration: .4s,.8s,.8s,.8s;
}
.home__popup.popup-chrome:before {
    left: 50%;
    top: 50%;
    right: auto;
    bottom: auto;
    width: 100%;
    padding-top: 100%;
    transform: scale(0) translate(-50%,-50%);
    transform-origin: var(--x) var(--y);
    min-height: 100%;
}
@media (max-width: 4500.98px) and (min-width: 1023.98px) {
    .home__popup.popup-safari{
        position: relative;
        overflow: hidden;
        transition: 0.5s;
        &:hover{
            background: #fff;
            cursor:pointer;
        }
    }
    .home__popup:hover span {
        color: #0b1f2f;
        z-index: 1;
    }
    .home__popup.popup-chrome:hover:before {
        transform: scale(1) translate(-50%, -50%);
        background-color: #fff;
    }
    .home__popup.popup-chrome:hover:after {
        transform: scale(.96);
        transition-timing-function: step-end;
        background-color: hsla(0deg,0%,100%,.05);
    }
}
